class Hydrator
{
  constructor({
    html,
    hydrate
  })
  {
    this.html    = html
    this.hydrate = hydrate
  }

  hydrate({ app, props, page })
  {
    const Page = this.locator.locate(`view/page/${page}`)
    this.hydrate(html`<${Page} ...${props}/>`, app)
  }
}

module.exports = Hydrator

